@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: "Press Start 2P", cursive, -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/memory-game/bg.png) no-repeat center center fixed;
  background-size: cover;
  text-align: center;
  position: relative;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-direction: row;
  width: 40em;
  margin: auto;
  flex-wrap: wrap;
  margin-top: 5%;
}

.card {
  -webkit-animation: spin 0.4s linear forwards;
          animation: spin 0.4s linear forwards;
  height: 10rem;
}

.main {
  position: relative;
}

.win {
  background-size: cover;
  background-position: center;
  background-image: url("/memory-game/bg-win.jpeg");
  position: absolute;
  border: 1px solid black;
  height: 30%;
  width: 100%;
  font-size: 2em;
  top: 30%;
  background-color: white;
  left: 0;
  margin-bottom: 100px;
  color: black;
  text-align: left;
  padding-top: auto;
  visibility: visible;
  -webkit-animation: hidden 2s linear forwards;
          animation: hidden 2s linear forwards;
}

.win h2 {
  -webkit-animation: defilement-rtl 2s ease forwards;
          animation: defilement-rtl 2s ease forwards;
  vertical-align: middle;
  visibility: visible;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes defilement-rtl {
  50% {
    transform: translate3d(40%, 0, 0); /* position finale à gauche */
  }
  100% {
    transform: translate3d(100%, 0, 0); /* position finale à gauche */
    visibility: hidden;
  }
}

@keyframes defilement-rtl {
  50% {
    transform: translate3d(40%, 0, 0); /* position finale à gauche */
  }
  100% {
    transform: translate3d(100%, 0, 0); /* position finale à gauche */
    visibility: hidden;
  }
}

@-webkit-keyframes hidden {
  100% {
    visibility: hidden;
  }
}

@keyframes hidden {
  100% {
    visibility: hidden;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

